import './App.css';
import TopIntro from './TopIntro.js';
import GlobalMapIntro from './GlobalMapIntro.js';
import GlobalMap from './GlobalMap.js';
import GlobalChart from './GlobalChart.js';
import React, { useEffect } from 'react';


// https://www.kaggle.com/datasets/iamsouravbanerjee/inequality-in-education-around-the-world?resource=download


const App = () => {
  useEffect(() => {
    const handleScroll = () => {
      let scrolled = window.pageYOffset;
      setTimeout(() => {
        if (scrolled === window.pageYOffset) {
          if (scrolled % window.innerHeight >= window.innerHeight / 2) {
            window.scrollBy(
              0,
              window.innerHeight - (scrolled % window.innerHeight)
            );
          } else {
            window.scrollBy(0, -(scrolled % window.innerHeight));
          }
        }
      }, 300);
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <div className="App">

      <script src="https://d3js.org/d3.v7.min.js"></script>

      <script src="https://d3js.org/topojson.v3.min.js"></script>

      <TopIntro></TopIntro>
      <GlobalMapIntro></GlobalMapIntro>
      <GlobalMap></GlobalMap>
      <GlobalChart></GlobalChart>

    </div>
  );
}

export default App;
